nav {
  clip-path: polygon(100% 0, 100% 5rem, 6rem 100%, 0 100%, 0 0);
}

.top-bar {
  display: flex;
  flex-grow: 1;
  height: 80px;
  padding: 20px 0;
  margin-left: -16px;
  z-index: 2;
  background: rgb(215, 31, 6);
  background: rgb(215, 31, 6);
  background: linear-gradient(133deg, rgb(215, 31, 6) 0%, rgb(215, 31, 6) 40%, rgb(226, 88, 70) 60%, rgb(215, 31, 6) 100%);
  box-shadow: 32px 8px 8px 0px rgba(0, 0, 0, 0.5);
}

.big-button {
  cursor: pointer;
  height: 60px;
  width: 60px;
  border-radius: 50%;
  border: 4px solid white;
  box-shadow: 0 0 8px 2px rgba(0, 0, 0, 0.1);
}

.big-button:hover {
  background: radial-gradient(#9af0ff, #00d0ff);
}

.big-button:disabled {
  background: gray;
  cursor: not-allowed;
}

.small-button {
  height: 12px;
  width: 12px;
  border-radius: 50%;
  margin: 0 4px;
  border: 1px solid rgba(0, 0, 0, 0.1);
}

.blue {
  background: radial-gradient(#9af0ff, #12b2d6);
}

.green {
  background: radial-gradient(#b0fb7b, #50fb05);
}

.yellow {
  background: radial-gradient(#ffdc26, #f3b438);
}

.red {
  background: radial-gradient(#da181f, #b90a0a);
}
